@import 'styles/index';

.accordionProduct {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  transition: width 1s 0.4s;
  &.isCompressed {
    width: 10%;
    cursor: pointer;
  }
  @media (--viewport-sm-only) {
    padding: 0 20px;
    &.isCompressed {
      width: 100%;
      cursor: default;
    }
  }
}

.closedForegroundImage {
  display: block;
  width: 50%;
  margin: 0 auto;
  img {
    width: 100%;
    height: auto;
  }
}

.closedBackgroundVideo {
  height: 100%;
}

.openBackgroundVideo {
  height: 100%;
}

.openBackgroundImage,
.closedBackgroundImage {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  img {
    object-fit: cover;
    object-position: center;
  }
}

.productImage {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
}

.accordionDefaultContent {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  transition: opacity 0.4s 1.4s;
  &.hidden {
    width: 0;
    height: 0;
    opacity: 0;
    transition: opacity 0.4s, height 0s 1.4s, width 0s 0.4s;
  }
  & > div {
    padding: 20px;
    text-align: center;
  }
}

.accordionExpanded {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.8s 1.4s;
  &.open {
    opacity: 1;
    transition: opacity 0.8s 0s;
  }
}

.accordionExpandedContent {
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  align-items: center;
  padding: 20px;
  opacity: 0;
  transform: translate3d(0, 25%, 0);
  transition: transform 0s 2s, opacity 0.4s 0s;
  @media (--viewport-sm-only) {
    flex-direction: column;
  }
}

.open .accordionExpandedContent {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 2s 0.4s ease, opacity 0.8s 1.8s;
}

.expandedAccordionImage {
  position: relative;
  width: 30%;
  @media (--viewport-sm-only) {
    width: 100%;
    flex: 1;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center bottom;
    }
  }
}

.closeBtn {
  position: absolute;
  z-index: 1;
  top: 20px;
  right: 20px;
  margin: 0;
}

.openBtn {
  margin: 1em 0 2em;
}

.openBtn,
.closeBtn {
  padding: 0;
  border-top: none;
  border-right: none;
  border-bottom: 1px solid;
  border-left: none;
  background: none;
  color: var(--color-dark-copy);
  cursor: pointer;
  font-family: var(--font-family-sans-serif-regular);
  &:focus {
    outline: 0;
  }
  &.light {
    color: var(--color-light-copy);
  }
}

.accordionDefaultBackground {
  height: 100%;

  /* transition: opacity 0.4s 1.4s;
  &.hidden {
    opacity: 0;
    transition: opacity 0.4s 0s;
  } */
}

.accordionOpenCopy,
.accordionClosedCopy {
  padding: 0;
  margin: 0 0 1em;
  font-family: var(--font-family-sans-serif-regular);
  @extend %font-theme-dark-copy;
  &.light {
    @extend %font-theme-light-copy;
  }
}

.buttonGroup {
  display: flex;
  margin: 1em 0 0;
  button,
  a {
    flex: 1;
  }
  button {
    margin: 0 4px 0 0;
  }
  @media (--viewport-sm-only) {
    display: block;
    button {
      margin: 0 0 4px;
    }
  }
}
